import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";

@Component({})
export default class App extends Vue {
  areaOptions: any[] = [];
  firstIndex: number | "" = "";
  secondIndex: number | "" = "";
  thirdIndex: number | "" = "";
  advanceValue: string[] = [];
  map!: any;
  currentAreaNode: any;
  districtExplorer!: any;
  tipMarker!: any;
  textMarkers!: any[];
  title: string = "";

  created(): void {}

  mounted() {
    this.textMarkers = [];
    this.initMap();
    this.getAreaData();
  }

  initMap() {
    this.map = new (window as any).AMap.Map(this.$refs.map, {
      center: [104.4976, 36.1697],
      resizeEnable: true,
      zoom: 4,
      mapStyle: "amap://styles/fe3e0ef8b301e96b14e8f1eeabbcece5"
    });
    (window as any).AMapUI.loadUI(["geo/DistrictExplorer"], (DistrictExplorer: any) => {
      this.initArea(DistrictExplorer);
    });
  }

  initArea(DistrictExplorer: any) {
    //创建一个实例
    var districtExplorer = (this.districtExplorer = new DistrictExplorer({
      map: this.map, //关联的地图实例
      eventSupport: true
    }));

    this.tipMarker = new (window as any).AMap.Marker({
      offset: new (window as any).AMap.Pixel(0, 0),
      bubble: true
    });

    //监听feature的hover事件
    districtExplorer.on("featureMouseout featureMouseover", (e: any, feature: any) => {
      this.toggleHoverFeature(feature, e.type === "featureMouseover", e.originalEvent ? e.originalEvent.lnglat : null);
    });

    //监听鼠标在feature上滑动
    districtExplorer.on("featureMousemove", (e: any, feature: any) => {
      //更新提示位置
      this.tipMarker.setPosition(e.originalEvent.lnglat);
    });

    districtExplorer.on("featureClick", (e: any, feature: any) => {
      var props = feature.properties;
      districtExplorer.locatePosition(
        e.originalEvent.lnglat,
        (error: any, routeFeatures: any[]) => {
          let route: any[] = [];
          routeFeatures.some((item: any) => {
            route.push(item);
            return item.properties.adcode === feature.properties.adcode;
          });
          this.switch2CurNode({
            adcode: props.adcode,
            name: route
              .slice(1)
              .map(d => d.properties.name)
              .join("-")
          });
        },
        {
          levelLimit: 4
        }
      );
    });

    //外部区域被点击
    districtExplorer.on("outsideClick", (e: any) => {
      districtExplorer.locatePosition(
        e.originalEvent.lnglat,
        (error: any, routeFeatures: any) => {
          if (routeFeatures && routeFeatures.length > 1) {
            //切换到省级区域
            this.switch2CurNode(routeFeatures[1].properties);
          } else {
            //切换到全国
            this.switch2CurNode({
              name: "",
              adcode: 100000
            });
          }
        },
        {
          levelLimit: 2
        }
      );
    });
    this.switch2AreaNode(100000);
  }

  // 根据Hover状态设置相关样式
  toggleHoverFeature(feature: any, isHover: boolean, position: any) {
    this.tipMarker.setMap(isHover ? this.map : null);
    if (!feature) {
      return;
    }
    var props = feature.properties;
    if (isHover) {
      //更新位置
      this.tipMarker.setPosition(position || props.center);
      //更新提示内容
      this.tipMarker.setContent("<div class='tip'>" + props.adcode + ": " + props.name + "</div>");
    }

    //更新相关多边形的样式
    var polys = this.districtExplorer.findFeaturePolygonsByAdcode(props.adcode);
    for (var i = 0, len = polys.length; i < len; i++) {
      polys[i].setOptions({
        // fillOpacity: isHover ? 1 : 0.8
      });
    }
  }

  //绘制某个区域的边界
  renderAreaPolygons(areaNode: any) {
    //更新地图视野
    this.map.setBounds(areaNode.getBounds(), null, null, true);
    //清除已有的绘制内容
    this.districtExplorer.clearFeaturePolygons();
    this.map.remove(this.textMarkers);
    //just some colors
    var colors = ["#7DBFF9", "#9FD4FB", "#4090F7", "#C3E6FC", "#2A6FD2"];
    //绘制子区域
    this.districtExplorer.renderSubFeatures(areaNode, (feature: any, i: any) => {
      var fillColor = colors[i % colors.length];
      var strokeColor = "#fff";
      // var strokeColor = colors[colors.length - 1 - (i % colors.length)];
      this.textMarkers.push(
        new (window as any).AMap.Text({
          map: this.map,
          text: feature.properties.name,
          position: feature.properties.center
        })
      );
      return {
        cursor: "default",
        bubble: true,
        strokeColor: strokeColor, //线颜色
        strokeOpacity: 1, //线透明度
        strokeWeight: 1, //线宽
        fillColor: fillColor, //填充色
        fillOpacity: 1 //填充透明度
      };
    });

    //绘制父区域
    this.districtExplorer.renderParentFeature(areaNode, {
      cursor: "default",
      bubble: true,
      strokeColor: "black", //线颜色
      strokeOpacity: 1, //线透明度
      strokeWeight: 1, //线宽
      fillColor: areaNode.getSubFeatures().length ? null : colors[0], //填充色
      fillOpacity: 1 //填充透明度
    });
    // this.map.setFitView(this.districtExplorer.getAllFeaturePolygons());
  }

  // 切换区域后刷新显示内容
  refreshAreaNode(areaNode: any) {
    this.districtExplorer.setHoverFeature(null);
    this.renderAreaPolygons(areaNode);
  }

  switch2CurNode(curNode: any) {
    this.title = curNode.name ? `${curNode.name}(${curNode.adcode})` : "";
    this.switch2AreaNode(curNode.adcode);
  }

  // 区域code显示区域
  switch2AreaNode(adcode: any) {
    if (this.currentAreaNode && "" + this.currentAreaNode.getAdcode() === "" + adcode) {
      return;
    }
    this.loadAreaNode(adcode, (error: any, areaNode: any) => {
      if (!error) {
        this.currentAreaNode = areaNode;
        //设置当前使用的定位用节点
        this.districtExplorer.setAreaNodesForLocating([this.currentAreaNode]);
        this.refreshAreaNode(areaNode);
      } else {
        (window as any).AMap.plugin("AMap.DistrictSearch", () => {
          let level = "province";
          this.secondIndex && (level = "city");
          this.thirdIndex && (level = "district");
          var districtSearch = new (window as any).AMap.DistrictSearch({
            level: level,
            subdistrict: 1
          });
          // 搜索所有省/直辖市信息
          districtSearch.search(adcode, (status: any, result: any) => {
            if (status === "complete") {
              console.log(result);
              result.districtList[0].districtList.forEach((item: any, index: number) => {
                // todo
                // this.switch2AreaNode(item.adcode);
              });
              // this.areaOptions = result.districtList[0].districtList;
            } else {
              this.$message.error("行政区域查询失败");
            }
          });
        });
      }
    });
  }

  // 加载区域
  loadAreaNode(adcode: any, callback?: any) {
    this.districtExplorer.loadAreaNode(adcode, (error: any, areaNode: any) => {
      if (error) {
        if (callback) {
          callback(error);
        }
        console.error(error);
        return;
      }
      if (callback) {
        callback(null, areaNode);
      }
    });
  }

  // 获取区域树数据
  getAreaData() {
    (window as any).AMap.plugin("AMap.DistrictSearch", () => {
      var districtSearch = new (window as any).AMap.DistrictSearch({
        // 关键字对应的行政区级别，country表示国家
        level: "country",
        //  显示下级行政区级数，1表示返回下一级行政区
        subdistrict: 3
      });

      // 搜索所有省/直辖市信息
      districtSearch.search("中国", (status: any, result: any) => {
        if (status === "complete") {
          this.areaOptions = result.districtList[0].districtList;
        } else {
          this.$message.error("行政区域查询失败");
        }
      });
    });
  }

  // 选择一级区域
  handleSelectFirst() {
    if (this.firstIndex !== "") {
      this.switch2CurNode(this.areaOptions[this.firstIndex]);
    } else {
      this.secondIndex = "";
      this.thirdIndex = "";
      //切换到全国
      this.switch2CurNode({
        name: "",
        adcode: 100000
      });
    }
    this.advanceValue = [];
  }

  // 选择二级区域
  handleSelectSecond() {
    if (this.secondIndex !== "") {
      let curNode = this.areaOptions[Number(this.firstIndex)].districtList[this.secondIndex];
      this.switch2CurNode({
        adcode: curNode.adcode,
        name: [this.areaOptions[Number(this.firstIndex)], curNode].map((d: any) => d.name).join("-")
      });
    } else {
      this.thirdIndex = "";
      this.handleSelectFirst();
    }
    this.advanceValue = [];
  }

  // 选择三级区域
  handleSelectThird() {
    if (this.thirdIndex !== "") {
      let curNode = this.areaOptions[Number(this.firstIndex)].districtList[Number(this.secondIndex)].districtList[this.thirdIndex];
      this.switch2CurNode({
        adcode: curNode.adcode,
        name: [this.areaOptions[Number(this.firstIndex)], this.areaOptions[Number(this.firstIndex)].districtList[Number(this.secondIndex)], curNode]
          .map((d: any) => d.name)
          .join("-")
      });
    } else {
      this.handleSelectSecond();
    }
    this.advanceValue = [];
  }

  // 普通查询
  handleSearch() {
    if (this.firstIndex !== "") {
      if (this.secondIndex !== "") {
        if (this.thirdIndex !== "") {
          this.handleSelectThird();
        } else {
          this.handleSelectSecond();
        }
      } else {
        this.handleSelectFirst();
      }
    } else {
      //切换到全国
      this.switch2CurNode({
        name: "",
        adcode: 100000
      });
      this.advanceValue = [];
    }
  }

  // 高级查询框筛选
  handleSelectAdvance() {
    if (this.advanceValue.length) {
      let route: any[] = [];
      let curOptions = this.areaOptions;
      this.advanceValue.forEach((item: any) => {
        let curNode = curOptions.find((d: any) => d.adcode === item);
        route.push(curNode);
        curOptions = curNode.districtList;
      });
      this.switch2CurNode({
        adcode: this.advanceValue[this.advanceValue.length - 1],
        name: route.map(d => d.name).join("-")
      });
    } else {
      //切换到全国
      this.switch2CurNode({
        name: "",
        adcode: 100000
      });
    }
    this.firstIndex = "";
    this.secondIndex = "";
    this.thirdIndex = "";
  }
}
