import microCoreVue from "common-config/lib/utils/microCoreVue";
import Vue, { CreateElement } from "vue";
import App from "./App.vue";
import "./plugins/index";
import { loadAMap, loadAMapUI } from "common-config/esm/utils/loadAMap";

Vue.config.productionTip = false;

const vueLifecycle = microCoreVue({
  Vue,
  appOptions: () => {
    return {
      render: (h: CreateElement) => h(App)
    };
  }
});

export const bootstrap = async function() {
  try {
    await Promise.all([loadAMap(), loadAMapUI()]);
  } catch (e) {
    //
  } finally {
    vueLifecycle.bootstrap();
  }
};

export const mount = vueLifecycle.mount;

export const unmount = vueLifecycle.unmount;
